___CSS_LOADER_EXPORT___.push([module.id, `html {
  font-family: sans-serif;
  line-height: 1.5;
}

video {
  width: 400px;
  height: 300px;
  border: 1px solid black;
}

.smallPhoto {
  height: 150px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-width: 400px;
}

/* Style for input fields */
.overlay-content input {
  width: 100%;
  margin-bottom: 10px;
}

/* Style for buttons */
.overlay-content button {
  margin-right: 10px;
}

.overlay-content button:last-child {
  margin-right: 0;
}

.dropdown {
  position: relative;
  display: block;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.close-button:hover {
  color: red;
}
